<template>
	<div>
		<router-view :keys='$route.params.pa'/>
	</div>
</template>

<script>
	import Vue from 'vue'
	import VueRouter from 'vue-router'
	import home from './home'
	Vue.use(VueRouter)
	var router = new VueRouter({
		routes: [
		{
			path: '/:pa',
			name: 'home',
			component: home
		}
		]
	})
	export default{
		router: router,
		created:function(){},
		beforeCreate:function(){}
	}
</script>

<style>
</style>


